.search-form {
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 10px;
  display: flex;
}

input {
  background-color: #f2f2f2;
  border: none;
  border-radius: 20px;
  outline: none;
  width: 800px;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .3s;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.image {
  width: 300px;
}

.stats-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  display: flex;
}

.stats {
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.photo-card {
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 10px #0000001a;
}

.load {
  color: #fff;
  cursor: pointer;
  background-color: gray;
  border: none;
  border-radius: 10px;
  margin: 20px auto;
  padding: 12px 24px;
  font-size: 16px;
}

.basicLightbox {
  backdrop-filter: blur(3px);
  background: #000c;
}

.custom-image {
  border-radius: 20px;
  max-width: 90vw;
  max-height: 90vh;
}

.custom-close {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  margin: 0;
  padding-bottom: 6px;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: -15px;
  right: -220px;
}

.custom-close:hover {
  background: #fff3;
}
